@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital@0;1&display=swap');

:root {
  --primary: #1f1f1f;
  --secondary: rgb(189, 9, 9);
  --white: rgb(240, 248, 255);
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  min-height: 100%;
  width: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  background-color: #1f1f1f;
  color: aliceblue;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  background-color: inherit;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.container{
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}

.header{
  height: 80px;
  background-color: #101010;
}

.header__container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.header__logo{
  font-size: 44px;
  color: aliceblue;

  span{
    color: crimson;
  }
}

.header__nav-list{
  display: flex;

}

@media (max-width: 768px) {
  .header__nav-list{
    display: none;
  }
}

.header__nav-item{
  margin-left: 30px;
}

/* Movie Page */
.movie-page {
  .subheader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
  }
}
.subheader{
  .heading{
    padding: 20px 0;
    text-align: left;
    color: var(--white);
    font-size: 40px;
  }
}

.header-input{
  padding: 20px 0;
  font-size: 1.125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  input{
    color: var(--white);
    padding: 10px 15px;
    width: 450px;
    background-color: transparent;
    border: 1px solid crimson;
    border-radius: 999px;
  }
}

.count-pill {
  display: inline-block;
  background-color: var(--secondary);
  padding: 5px 15px;
  //font-size: 1.125rem;
  font-weight: 600;
  border-radius: 999px;
  color: var(--primary);
}

.plug{
  opacity: .65;
  text-align: center;
  margin-top: 60px;
}

.movie-page {
  padding: 30px 0;
  width: 100%;
  font-size: 45px;
  margin: 0;
  color: var(--white);
}

.movie-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 30px;
  align-items: center;
  justify-items: center;
}

@media (max-width: 992px) {
  .movie-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .movie-grid {
    grid-template-columns: repeat(1, 1fr);
  }
}

.movie-card {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  border: 3px solid transparent;
  display: block;
  transition: all 0.3s ease;
  border-radius: 15px;
}

.movie-card:hover{
  border: 3px solid var(--secondary);
}

.movie-card {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.movie-card {
  .overlay {
    text-align: center;
    padding: 15px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease;
    z-index: 1;
    transform: translateY(100%);

    p {
      font-size: 14px;
    }

    h3 {
      font-weight: 700;
      font-size: 24px;
      margin-bottom: 10px;
    }
  }
}
.movie-card:hover {
  .overlay {
    background-color: rgba(96, 4, 4, 0.5);
    transform: translateY(0);

  }
}
//pagination
.pagination{
  text-align: center;
  padding-top: 20px;
}

.pagination-number{
  margin: 0 10px;
  opacity: 0.7;
  font-size: 20px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.pagination-number:hover{
  opacity: 1;
}

.active-page{
  color: crimson;
  opacity: 1;
  font-size: 22px;
}

//preloader
.preloader{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 700px;
}

@media (max-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 970px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 750px;
  }
}